import { Component} from '@angular/core';
import { NavController , Platform} from '@ionic/angular';
import { SpeechRecognition } from '@ionic-native/speech-recognition/ngx';
import { ChangeDetectorRef } from '@angular/core';
@Component({
  selector: 'app-speech',
  templateUrl: './speech.page.html',
  styleUrls: ['./speech.page.scss'],
})
export class SpeechPage{

  // Declares the required variables before the constructor.
  public matches: String[] = ['Result of speech Recording is showed here'];
  isRecording = false;

  //Inject to the constructor.
  constructor(
      public navCtrl: NavController,
      private speechRecognition: SpeechRecognition,
      private plt: Platform,
      private cd: ChangeDetectorRef
    ) { }
  // function for start recording audio on smartphone
   stopListening() {
    this.speechRecognition.stopListening().then(() => {
      this.isRecording = false;
    });
  }
  getPermission() {
    this.speechRecognition.hasPermission()
      .then((hasPermission: boolean) => {
        if (!hasPermission) {
          this.speechRecognition.requestPermission();
        }
      });
  }

  startListening() {
  this.matches = [];
    let options = {
      language: 'en-US'
    }
    this.speechRecognition.startListening(options).subscribe(matches => {
      this.matches = matches;
      this.cd.detectChanges();
    });
    this.isRecording = true;
  }

}
